.homePage{
    /* background-image: url("https://i.imgur.com/haBMi7g.jpg"); */
    /* background-image: url("./../../images/hikingApp.png"); */
    background-size: 100% 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin: 0;
}

.homePageCol{
    width: 100%; 
    height: 100vh;
    margin: 0;
    padding: 0;
}

#homePageName{
    position: absolute;
    margin: 0;
    width: 100%;
    text-align:center;
    font-size: 6em;
    top: 12%;
    text-shadow: 3px 2px 5px black;
    /* font-family: 'Arvo', serif; */
    /* font-family: 'Domine', serif; */
    font-family: 'Roboto', sans-serif;

}

#homePageTitle{
    position: absolute;
    margin: 0;
    width: 100%;
    text-align: center;
    font-size: 6em;
    top: 25%;
    text-shadow: 3px 2px 5px black;
    /* font-family: 'Arvo', serif; */
    /* font-family: 'Domine', serif; */
    font-family: 'Roboto', sans-serif;

}

@media (orientation: landscape) {
    .homePage{
        background-image: url("https://i.imgur.com/haBMi7g.jpg");
        /* background-image: url("./../../images/DSC_0949.JPG"); */
        background-size: 100% 100%;
    }
}

@media (orientation: portrait) {
    .homePage{
        /* background-image: url("./../../images/DSC_0839.JPG"); */
        background-size: 100% 100%;
    }

    #homePageName {
        font-size: 4rem;
        color: #ffffff;
    }

    #homePageTitle {
        font-size: 3.5rem;
        color: #ffffff;
        top: 45%;
    }
}